/* src/TopRow.css */
.top-row {
  background-color: #282c34;
  color: #fff;
  padding: 1rem;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; /* Allows elements to wrap to the next line */
  width: 100%; /* Ensures full width */
  box-sizing: border-box; /* Ensures padding is included in width calculation */
  max-width: 100vw; /* Prevents horizontal scrolling */
  width: 100%; /* Ensures full width */
  font-family: Helvetica, Arial, sans-serif;
  /* padding-bottom: 20px; */
}

.usc-logo {
  max-height: 50px;
  width: auto; /* Maintain aspect ratio */
  margin-left: auto; /* Optional: add some space between logo and title */
  margin-right: 10px; /* Optional: add some space between logo and title */
}

.right-section {
  display: flex; /* Align the link and button in a row */
  align-items: center;
}

.toggle-docs-btn, .download-link {
  background-color: #f1f1f1;
  color: black;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-left: 10px; /* Adds space between link and button */
  margin-right: 10px; /* Adds space between link and button */
  text-decoration: none; /* Remove underline from link */
  display: inline-block; /* Align link with the button */
}

#toggle-docs-btn-id{
  margin-right: 20px;
}

.toggle-docs-btn:hover, .download-link:hover {
  background-color: #d0d3d7;
}

.top-row h2 {
  text-align: left;
  padding-left: 20px;
  font-size: 2rem;
  margin-right: 10px;
}

.main {
  color: inherit; /* blue colors for links too */
  text-decoration: inherit; /* no underline */
}



.button-container {
  display: flex; /* Makes the buttons align horizontally */
  align-items: center; /* Aligns buttons vertically */
  margin-bottom: 10px; /* Adds some space below the buttons when they wrap */
}

.buttons-and-logo {
  display: flex; /* Aligns buttons and logo horizontally */
  align-items: center; /* Ensures vertical alignment */
  flex-wrap: wrap; /* Allows these elements to wrap */
}

/* Media query for small screens */
@media (max-width: 600px) {
  .top-row {
      justify-content: center; /* Centers content on smaller screens */
  }

  .button-container {
      justify-content: center; /* Centers buttons */
  }

  .buttons-and-logo {
      justify-content: center; /* Centers buttons and logo */
      flex-direction: column; /* Stacks them vertically */
  }

  .toggle-docs-btn, .download-link {
    font-size: 12px;
  }

  .usc-logo {
      margin-left: 0; /* Removes margin on smaller screens */
      margin-bottom: 10px; /* Adds some space below the logo */
      margin-top: 10px; /* Adds some space below the logo */
  }
  .top-row h2 {
    padding-left: 0px;
  }
}